import { combineReducers } from 'redux';
import { SET_RECIPES } from '../actions/setRecipes';
import { FAVORITE_RECIPES } from '../actions/favoriteRecipes';

function recipes (state = [], action) {
  switch (action.type) {
    case SET_RECIPES:
      return action.items; 
     
    default:
      return state;
  }
}

function favoriteRecipes(state = [], action) {
  switch (action.type) {
    case FAVORITE_RECIPES:
      state = [...state, action.recipe]
      return state;

    default:
      return state;
  }
}

const rootReducer = combineReducers({recipes, favoriteRecipes});

export default rootReducer;



